import React from "react";
import Modal from "@mui/material/Modal";
import { TextField, Box, Button, Typography, Snackbar } from "@mui/material";
import Alert from "@mui/material/Alert";
import { modalStyle } from "./styles/modalStyles";

function ImportModal({ setDataItems, onClose }) {
    const [importData, setImportData] = React.useState('');
    const [open, setOpen] = React.useState(true);
    const [alertOpen, setAlertOpen] = React.useState(false);

    const handleClose = () => { setOpen(false); onClose(); };
    const handleAlertClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setAlertOpen(false);
    };


    const handleImport = () => {
        try {
            const importedData = JSON.parse(importData);

            // 데이터 검증
            if (!Array.isArray(importedData)) {
                throw new Error('데이터가 배열이 아니에요');
            }

            importedData.forEach((item) => {
                if(!item.id || !item.data || !item.interval) {
                    throw new Error('필수 필드가 없어요!..ㅜ-ㅜ (id, data, interval)');
                }
            })

            // 정상적인 데이터
            setDataItems(importedData);
            localStorage.setItem('dataItems', JSON.stringify(importedData));
            setAlertOpen(true);
        } catch (error) {
            alert(`${error}`);
        }
    };

    return (

        <Modal
            open={open}
            onClose={onClose}
            aria-labelledby="modal-modal-title"
        >
            <Box sx={modalStyle}>
                <Typography variant="h6" id="modal-modal-title" component="h2">데이터 붙여넣깅</Typography>
                <TextField
                    id='input-import-textfield'
                    label="데이터를 붙여넣어주세용"
                    multiline
                    rows={10}
                    value={importData}
                    onChange={(e) => setImportData(e.target.value)}
                    variant="outlined"
                    fullWidth
                    margin="normal"
                ></TextField>

                <Box mt={2} display={"flex"} justifyContent={"flex-end"}>
                    <Button sx={{ marginRight:'10px'}} onClick={handleImport}>얍</Button>
                    <Button onClick={handleClose}>닫기</Button>
                </Box>

                <Snackbar
                    open={alertOpen}
                    autoHideDuration={3000}
                    onClose={handleAlertClose}
                    anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                >
                    <Alert onClose={handleAlertClose} severity="success" sx={{ width: '100%' }}>
                        데이터 가져왔어용~<br></br>잘 사용하세용~
                    </Alert>
                </Snackbar>
            </Box>
        </Modal>
    );
}

export default ImportModal;


