import React from "react";

import { Fab, Typography } from "@mui/material";
import InfoIcon from "@mui/icons-material/Info";
import Snackbar from "@mui/material";
import { modalStyle } from "../../styles/modalStyles";
import { Modal, Box, Button } from "@mui/material";
export default function InformationButton() {
    const [infoModalOpen, setInfoModalOpen] = React.useState(false);

    const fabStyle = {
        position: 'fixed',
        bottom: '20px',
        right: '20px',
    };

    const openWebSite = () => {
        window.open('https://github.com/Establers/establers.github.io/issues',
            '_blank', 'noopener', 'noreferrer');

    }

    const handleClose = () => {
        setInfoModalOpen(false);
    };

    const handleOpen = () => {
        setInfoModalOpen(true);
    };

    return (
        <>
        <Fab 
            sx={fabStyle} 
            size="small" 
            color="primary" 
            variant="extended" 
            onClick={handleOpen}
        >
            <InfoIcon />
        </Fab>

        <Modal
            open={infoModalOpen}
            onClose={handleClose}
            aria-labelledby="information-modal-title"
            aria-describedby="information-modal-description"
        >
            <Box sx={modalStyle}>
                <Typography id='information-modal-title' variant="h6" component="h2">
                    😎 유아튜파잉 uartupying
                </Typography>
                <Typography id="information-modal-description" sx={{mt : 2}}>
                    안녕하세요! <br></br>
                    재미로 만든 간단 UART 테스트 툴! 송신 위주! <br></br>
                    기능 제안은 언제든지 환영합니다! <br></br>
                    하지만, 이건 그냥 재미로 만든거라서... <br></br>
                    기능 추가는 느리게 할거에요... <br></br>
                    버그 제보는 환영합니다! <br></br>
                    그럼 즐거운 테스트 되세용~ 😎
                    <Box mt={2} display="flex" justifyContent="flex-end">
                    <Button sx={{ marginRight:'10px'}} onClick={ () => {
                        openWebSite();
                    }}>버그 리포트 하러가기</Button>
                    <Button onClick={handleClose}>닫기</Button>
                    </Box>
                </Typography>
            </Box>
        </Modal>
        </>
    );
}