import { useContext, useCallback, useEffect } from "react";
import SerialPortContext  from "./SerialPortContext";

const useSerialPort = () => {
    const { port, setPort, logs, setLogs, dataItems, setDataItems, writerRef, readerRef, isSendingRef } = useContext(SerialPortContext);
    const testMode = true;

    // 16진수 문자열을 Uint8Array로 변환
    const hexStringtoByteArray = (hexString) => {
        const cleanHexString = hexString.replace(/\s+/g, '');   // 공백 제거
        const bytes = [];
        for(let i = 0; i < cleanHexString.length; i += 2) {
            const byte = cleanHexString.substr(i, 2);   // 2자리씩 자르기
            bytes.push(parseInt(byte, 16));             // 16진수 문자열을 10진수 정수로 변환
        }
        return new Uint8Array(bytes);   // Uint8Array로 변환
    };

    // 데이터 송신부 처리
    const sendDataItem = useCallback(async (item, testMode = true) => {
        console.log('sendDataItem');
        if(!port && !testMode) {
            console.log("포트가 연결안됐는디용~");
            return; // X
        }
        
        if(testMode){
            console.log(hexStringtoByteArray(item.data));
            // const timestamp = new Date().toLocaleTimeString('ko-KR');
            const now = new Date();
            const timestamp = `${now.toLocaleTimeString('ko-KR')}.${now.getMilliseconds().toString().padStart(3, '0')}`;
            setLogs((prevLogs) => [...prevLogs, { timestamp, message: `TX : ${item.data}`}]);
        }

        let writer; // const 대신 let으로 변경 why : writer.releaseLock() 때문에
        try {
            writer = port.writable.getWriter();
            writerRef.current = writer; // writerRef에 writer 저장

            const dataArray = hexStringtoByteArray(item.data);
            await writer.write(dataArray);  // 데이터 송신
            
            const now = new Date();
            const timestamp = `${now.toLocaleTimeString('ko-KR')}.${now.getMilliseconds().toString().padStart(3, '0')}`;
            setLogs((prevLogs) => [...prevLogs, { timestamp, message: `TX : ${item.data}`}]);
        } catch (error) {
            console.error('데이터 송신 실패 ㅠ.ㅠ : ', error);
        } finally {
            if(writerRef.current) {
                writerRef.current.releaseLock();   // writer 잠금 해제 
                writerRef.current = null;          // writerRef 초기화
            }
        }
    }, [port, setLogs, testMode]
);

    // 데이터 수신부 처리
    const readData = useCallback(async () => {
        if(!port) {
            console.log("포트가 연결 안 됐는디용~");
            return;
        }
        
        // 데이터 수신
        try {
            readerRef.current = port.readable.getReader(); // readerRef에 reader 저장

            while (true) {
                const { value, done } = await readerRef.current.read();
                if (done) {
                    // 스트림이 닫혔을 때 루프 종료
                    console.log('읽기 스트림이 닫혔습니다.');
                    break;
                }
                if (value) {
                    const hexValue = Array.from(value)
                        .map((byte) => byte.toString(16).padStart(2, '0'))
                        .join(' ');
                    
                    const now = new Date();
                    const timestamp = `${now.toLocaleTimeString('ko-KR')}.${now.getMilliseconds().toString().padStart(3, '0')}`;
                    setLogs((prevLogs) => [...prevLogs, { timestamp, message: `RX : ${hexValue}`}]);
                }
            }
        } catch (error) {
            console.error('데이터 수신 실패 ㅠ.ㅠ : ', error);
        } finally {
            if (readerRef.current) {
                readerRef.current.releaseLock();
                readerRef.current = null;
                console.log('읽기 스트림 해제 마지막');
            }
        }
        
    }, [port, setLogs, readerRef, testMode]);

    // 포트 연결 해제 함수
    const disconnectPort = useCallback(async () => {
        if(!port) return;

        try {
            // 전송 중지
            isSendingRef.current = false;

            // 읽기 스트림 취소
            if (readerRef.current) {
                await readerRef.current.cancel();
                readerRef.current = null;
            }

            // 쓰기 스트림 해제
            if (writerRef.current) {
                await writerRef.current.releaseLock();
                writerRef.current = null;
            }

            // 포트 닫기
            if (port) {
                await port.close();
                setPort(null);
                console.log('Serial port closed successfully.');
            }
        } catch (error) {
            console.error('포트 닫기 실패:', error);
            alert('연결 끊기 실패 ㅠ.ㅠ');
            throw error;
        }
    }, [port, setPort, writerRef, readerRef]);

    // 포트 연결 함수
    const connectPort = useCallback(async (options = { baudRate: 9600, dataBits: 8, stopBits: 1, parity: 'none' }) => {
        try {
            const selectedPort = await navigator.serial.requestPort();
            await selectedPort.open(options);
            setPort(selectedPort);

            // 데이터 수신 시작
            // readData(selectedPort);
            console.log('readData 시작');

            // 포트가 닫히면 상태에서 제거
            const handleDisconnect = () => {
                disconnectPort();
            };
            selectedPort.addEventListener('disconnect', handleDisconnect);

            return () => {
                selectedPort.removeEventListener('disconnect', handleDisconnect);
            }

            // 설정 저장
            // localStorage.setItem('serialConnectSettings', JSON.stringify(
            //     {connectBaudRate, dataBits, stopBits, parity}
            // ));
            
        } catch (error) {
            console.error('! Error connecting:', error);
            alert('연결 실패 ㅠ.ㅠ');
            throw error;
        }
    }, [setPort, readData, disconnectPort]);

    useEffect(() => {
        if(port && port.readable) {
            readData();
        }
        
        return () => {
            if(readerRef.current) {
                readerRef.current.cancel().catch((error) => {
                    console.error('! Reader Cancel 실패..:', error);
                });
            }
        }
    }, [port, readData]);

    return {
        port,
        logs,
        dataItems,
        setLogs,
        setDataItems,
        setPort,
        setLogs,
        connectPort,
        disconnectPort,
        sendDataItem,
        readData,
        isSendingRef,
    };
}

export default useSerialPort;