// Writer, Reader 를 공용으로 사용하기 위해 Context
import React, { createContext, useRef, useState, useEffect } from 'react';
import { v4 as uuidv4 } from 'uuid';  // 고유한 ID 생성 라이브러리

const SerialPortContext = createContext();

export const SerialPortProvider = ({ children }) => {
    const [port, setPort] = useState(null);
    const [logs, setLogs] = useState([]);
    const [dataItems, setDataItems] = useState([{ id: uuidv4(), data: '', interval: 1000, description: ''  }]);

    const writerRef = useRef(null);
    const readerRef = useRef(null);
    const isSendingRef = useRef(false);

    // LocalStorage 에서 데이터 아이템 불러오기
    useEffect(() => {
        const savedDataItems = localStorage.getItem('dataItems');
        if (savedDataItems) {
            try {
                const parsedItems = JSON.parse(savedDataItems).map((item) => ({
                    ...item,                  // 기존 데이터 복사
                    id: item.id || uuidv4(),  // id가 없으면 새로 생성
                }));
                setDataItems(parsedItems);
            } catch (error) {
                console.error('dataItems 로드 실패:', error);
                // 초기값으로 설정하거나 다른 조치를 취할 수 있습니다.
            }
        }
    }, []);

    // dataItems가 변경될 때마다 localStorage에 저장
    useEffect(() => {
        try {
            localStorage.setItem('dataItems', JSON.stringify(dataItems));
        } catch (error) {
            console.error('dataItems 저장 실패:', error);
        }
    }, [dataItems]);

    return (
        <SerialPortContext.Provider 
            value={{ 
                port, setPort, 
                logs, setLogs, 
                dataItems, setDataItems, 
                writerRef, readerRef, isSendingRef 
            }}>
            {children}
        </SerialPortContext.Provider>
    );
};

export default SerialPortContext;